.container {
    @include mq(tablet) {
         padding-right: 32px;   
         padding-left: 32px;
    }

    @include mq(desktop) {
        padding-right: 16px;
        padding-left: 16px;
    }
}

.section-products {
    padding-top: 60px;
    padding-bottom: 60px;

    &__label {            
        margin-bottom: 15px;

        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 18px;
        line-height: 1.14;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;

        color: #FFA5BA;
        
        @include mq(tablet) {
            margin-bottom: 18px;
            font-size: 26px;
        }

        @include mq(desktop) {
            margin-bottom: 16px;
            font-size: 30px;
        }            
    }

    &__title {
        margin-bottom: 148px;  

        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 34px;
        line-height: 1.14;
        text-align: center;        
        letter-spacing: 0.04em;
        text-transform: uppercase;

        color: #D41443;

        @include mq(tablet) {                
            margin-bottom: 158px;
            font-size: 48px;
        }

        @include mq(desktop) {
            margin-bottom: 281px;
            font-size: 58px;
        }
    }

}

.products {
    @include mq(tablet) {
        display: flex;
        justify-content: center;
        margin-right: auto; 
        margin-left: auto;       
    }

    &__item {

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative; 
                       
        margin-left: auto;
        margin-right: auto;

        
        padding-top: 160px;
        padding-left: 40px;
        padding-right: 40px;
        padding-bottom: 40px;
        border-radius: 24px;        

        max-width: 280px;
        
        
        
        
        @include mq(tablet) {
            margin-left: 0px;
            margin-right: 0px;

            width: calc((100% - 32px) / 3);
            max-width: 224px;            
            padding-top: 118px;
            padding-left: 32px;
            padding-right: 32px;
            padding-bottom: 40px;

            max-width: 224px;
        }

        @include mq(desktop) {
            margin-left: 0px;
            margin-right: 0px;

            width: calc((100% - 64px) / 3);
            max-width: 368px;            
            padding-top: 165px;
            padding-left: 63px;
            padding-right: 63px;
            padding-bottom: 60px;

            max-width: 368px;
        }

        box-shadow: 0px 11px 10px -9px rgba(161, 159, 159, 1);
        -webkit-box-shadow: 0px 11px 10px -9px rgba(161, 159, 159, 1);
        -moz-box-shadow: 0px 11px 10px -9px rgba(161, 159, 159, 1);
    }  
    
    &__item:not(:last-child) {
        @include mq(mobile-only) {
            margin-bottom: 140px;
        }

        @include mq(tablet) {
            margin-right: 16px;
        }

        @include mq(desktop) {
            margin-right: 32px;
        }
    }

    &__item--cream {
        background-color: #FFB8CA;
    }

    &__item--coffee {
        background-color: #F0D1A5;
    }

    &__item--milk {
        background-color: #C2E297;
    }    
    
    &__title {
        position: relative;
        display: block;  
        
        margin-bottom: 70px;
        
        font-family: 'Titan One';
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 1.1;
        text-align: center;
        letter-spacing: 0.04em;
        text-transform: uppercase;     
        color: #FFFFFF;

        @include mq(desktop) {
            font-size: 30px;
            line-height: 1.15;
        }
    } 
    
    &__title::after {
        content: '';
        position: absolute;
        left: 50%;
        top: 52px;
        transform: translateX(-50%);

        @include mq(desktop) {
            top: 62px;
        }

        display: block;

        width: 26px;
        height: 6px;
        background-image: url(../images/products/svg/elips.svg);
        background-position: center;
        background-repeat: no-repeat;
    }

    &__text {    
        margin-bottom: 32px;
        
        font-family: 'DM Sans';
        font-style: normal;
        font-weight: 700;
        font-size: 14px;
        line-height: 1.52;

        text-align: center;
        letter-spacing: 0.04em;

        color: #FFFFFF;

        @include mq(tablet) {
            line-height: 1.76;
        }

        @include mq(desktop) {
            margin-bottom: 60px;
            font-size: 16px;
            line-height: 1.76;
        }
    }
    
         &__item::before {
             position: absolute;
             top: -140px;
             left: 0px;
    
             content: '';
             width: 100%;
             max-width: 280px;
             height: 280px;
             background-position: center;
             background-repeat: no-repeat;
             background-size: contain;
    
             @include mq(tablet) {
                top: -138px;
                 width: 100%;
                 max-width: 224px;
                 height: 224px;
             }
    
             @include mq(desktop) {
                top: -261px;
                 width: 100%;
                 max-width: 368px;
                 height: 394px;
             }
         }
    
         &__item--cream::before {
             background-image: url(../images/products/Products-1_sm.png);
    
             @media (min-device-pixel-ratio: 2),
             (min-resolution: 192dpi),
             (min-resolution: 2dppx) {
                 background-image: url(../images/products/Products-1_sm@2x.png);
             }
    
             @include mq(tablet) {
                 background-image: url(../images/products/Products-1_md.png);
    
                 @media (min-device-pixel-ratio: 2),
                 (min-resolution: 192dpi),
                 (min-resolution: 2dppx) {
                     background-image: url(../images/products/Products-1_md@2x.png);
                 }
             }
    
             @include mq(desktop) {
                 background-image: url(../images/products/Products-1_lg.png);
    
                 @media (min-device-pixel-ratio: 2),
                 (min-resolution: 192dpi),
                 (min-resolution: 2dppx) {
                     background-image: url(../images/products/Products-1_lg@2x.png);
                 }
             }
         }
    
         &__item--coffee::before {
             background-image: url(../images/products/Products-2_sm.png);
    
             @media (min-device-pixel-ratio: 2),
             (min-resolution: 192dpi),
             (min-resolution: 2dppx) {
                 background-image: url(../images/products/Products-2_sm@2x.png);
             }
    
             @include mq(tablet) {
                 background-image: url(../images/products/Products-2_md.png);
    
                 @media (min-device-pixel-ratio: 2),
                 (min-resolution: 192dpi),
                 (min-resolution: 2dppx) {
                     background-image: url(../images/products/Products-2_md@2x.png);
                 }
             }
    
             @include mq(desktop) {
                 background-image: url(../images/products/Products-2_lg.png);
    
                 @media (min-device-pixel-ratio: 2),
                 (min-resolution: 192dpi),
                 (min-resolution: 2dppx) {
                     background-image: url(../images/products/Products-2_lg@2x.png);
                 }
             }
         }
    
         &__item--milk::before {
             background-image: url(../images/products/Products-3_sm.png);
    
             @media (min-device-pixel-ratio: 2),
             (min-resolution: 192dpi),
             (min-resolution: 2dppx) {
                 background-image: url(../images/products/Products-3_sm@2x.png);
             }
    
             @include mq(tablet) {
                 background-image: url(../images/products/Products-3_md.png);
    
                 @media (min-device-pixel-ratio: 2),
                 (min-resolution: 192dpi),
                 (min-resolution: 2dppx) {
                     background-image: url(../images/products/Products-3_md@2x.png);
                 }
             }
    
             @include mq(desktop) {
                 background-image: url(../images/products/Products-3_lg.png);
    
                 @media (min-device-pixel-ratio: 2),
                 (min-resolution: 192dpi),
                 (min-resolution: 2dppx) {
                     background-image: url(../images/products/Products-3_lg@2x.png);
                 }
             }
         }
        

    &__button {
        display: flex;

        margin-right: auto;
        margin-left: auto;
        padding: 0;

        justify-content: center;
        align-items: center;

        height: 40px;
        width: 40px;
        border-radius: 50%;

        background-color: #FFFFFF;

        box-shadow:
                0px 1px 1px rgba(0, 0, 0, 0.12),
                0px 4px 4px rgba(0, 0, 0, 0.06),
                1px 4px 6px rgba(0, 0, 0, 0.16);        

        cursor: pointer;
        transform: scale(1);
        transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    }

    &__button:hover,
    &__button:focus {
        transform: scale(1.5);        
    }
           
    &__icon {
        fill: #D41443;
    }
}





